import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
        isIndex: true,
        bannerShow: true
    },
    mutations: {
        changeIndex(state, boolean) {
            state.isIndex = boolean
        },

        changeBanner(state, boolean) {
            state.bannerShow = boolean
        }
    }
})

export default store